<template>
  <div class="dispel" @click.stop>
    <div class="wrapper">
      <ul class="box" ref="boxDom" :class="{ac:isTransition}">
        <li
          ref="gameDom"
          @click.stop="select(item)"
          v-for="(item,index) in playList"
          :class="{ac:firstSelect===item.colRow, delete:item.delete}"
          :key="index"
          :style="{
            left:`${item.left}px`,
            top:`${item.top}px`,
            'background-position':item.pos
        }"
        ></li>
      </ul>
    </div>
  </div>
</template>

<script>
import { pic } from 'assets/dispel/dispelData.js'
import * as Utils from 'utils/Utils.js'
import { Transform } from 'stream';
import { async } from 'q';

const transition = Utils.prefixStyle('transition');
const transform = Utils.prefixStyle('transform');
export default {
  name: 'dispel',
  data() {
    return {
      axisData: {
        row: 8,
        col: 10,
        allCol: 13
      },
      isTransition: true,
      picList: pic,
      playList: [],
      firstSelect: '',
      lastSelect: ''
    }
  },
  methods: {
    init() {
      let row = this.axisData.row;
      let col = this.axisData.col;
      let allCol = this.axisData.allCol;
      if (this.$refs.boxDom) {
        this.$refs.boxDom.style[transform] = `translateY(-${(allCol - col) * 40}px)`
      }
      let arr = Array.from(new Array(row * allCol));
      arr.forEach((item, index) => {
        let data = this.picList[Math.floor(Math.random() * 6)];
        // let data = this.picList[index % 6];
        this.playList.push({
          id: data.id,
          pos: data.pos,
          delete: false,
          row: index % row,
          col: Math.floor(index / row),
          colRow: `${Math.floor(index / row)}-${index % row}`,
          left: (index % row) * 40,
          top: Math.floor(index / row) * 40
        })
      })
      this.$nextTick(() => this.remove())
    },
    // 交换
    async playGame() {
      return
      this.addAnimation();
      let firstIndex = this.playList.findIndex(item => {
        return item.colRow === this.firstSelect
      })
      let lastIndex = this.playList.findIndex(item => {
        return item.colRow === this.lastSelect
      })
      let lastData = this.$copy(this.playList[firstIndex]);
      await this.copyChange(this.playList[firstIndex], this.playList[lastIndex])
      await this.copyChange(this.playList[lastIndex], lastData)
      this.firstSelect = this.lastSelect = '';
      setTimeout(() => {
        this.remove();
      }, 300)
    },
    // 对调选择
    copyChange(obj, copyObj) {
      for (let key in copyObj) {
        if (key !== 'pos' && key !== 'id') obj[key] = copyObj[key]
      }
    },
    // 抵消消除
    remove() {
      let rowArr = this.toArrays();
      let colArr = this.toArrays(true);
      let colRowArr = rowArr || colArr;
      if (colRowArr) {
        this.playList.forEach(item => {
          if (item.colRow === colRowArr[0] || item.colRow === colRowArr[1] || item.colRow === colRowArr[2]) {
            item.delete = true;
          }
        });
        // 删除
        setTimeout(async () => {
          this.addAnimation(false);
          this.playList = await this.playList.filter((item, itemIndex) => {
            return item.colRow !== colRowArr[0] && item.colRow !== colRowArr[1] && item.colRow !== colRowArr[2]
          })
          await colRowArr[5] === 'row' ? this.removeRow(colRowArr) : this.removeCol(colRowArr);
        }, 300)
      }
    },
    // 消除横向
    removeRow(colRowArr) {
      // 下落
      setTimeout(async () => {
        this.addAnimation(true, 0.1);
        this.playList.forEach(item => {
          if (item.col <= colRowArr[3]) {
            for (let i = 0; i < 3; i++) {
              if (item.row === colRowArr[4] + i) {
                item.top += 40;
                item.col += 1;
                item.colRow = `${item.col}-${item.row}`;
              }
            }
          }
        })
        await this.addGame(colRowArr);
        await this.remove();
      }, 300)
    },
    // 消除纵向
    removeCol(colRowArr) {
      setTimeout(async () => {
        this.addAnimation(true, 0.1);
        this.playList.forEach(item => {
          if (item.row === colRowArr[4] && item.col < colRowArr[3]) {
            item.top += 40 * 3;
            item.col += 1 * 3;
            item.colRow = `${item.col}-${item.row}`;
          }
        })
        await this.addGame(colRowArr, true)
        await this.remove()
      }, 300)
    },
    // 添加game
    addGame(colRowArr, isCol = false) {
      for (let i = 0; i < 3; i++) {
        let data = this.picList[Math.floor(Math.random() * 6)];
        let startRow = colRowArr[4];
        this.playList.push({
          id: data.id,
          pos: data.pos,
          row: isCol ? startRow : startRow + i,
          col: isCol ? i : 1,
          colRow: isCol ? `${i}-${startRow}` : `1-${startRow + i}`,
          left: isCol ? startRow * 40 : (startRow + i) * 40,
          top: isCol ? i * 40 : 0
        })
      }
    },
    // 转二维数组
    toArrays(isCol = false) {
      let arr = []
      this.playList.forEach((item, index) => {
        if (!arr[item.top / 40]) arr[item.top / 40] = [];
        arr[item.top / 40].push(item)
      });
      arr.forEach(item => {
        item = item.sort((a, b) => {
          return a.left - b.left
        })
      })
      let colRowArr = null;
      for (let i = 0; i < arr.length; i++) {
        for (let j = 0; j < arr[i].length; j++) {
          if (arr[i][j + 1] &&
            arr[i][j + 2] &&
            arr[i][j].id === arr[i][j + 1].id &&
            arr[i][j].id === arr[i][j + 2].id) {
            colRowArr = [
              `${i}-${j}`,
              `${i}-${j + 1}`,
              `${i}-${j + 2}`,
              i,
              j,
              'row'
            ];
            break;
          }
          if (arr[i + 1] &&
            arr[i + 2] &&
            arr[i + 1][j] &&
            arr[i + 2][j] &&
            arr[i][j].id === arr[i + 1][j].id &&
            arr[i][j].id === arr[i + 2][j].id) {
            colRowArr = [
              `${i}-${j}`,
              `${i + 1}-${j}`,
              `${i + 2}-${j}`,
              i,
              j,
              'col'
            ];
            break;
          }
        }
      }
      return colRowArr
    },
    // 添加/删除动画
    addAnimation(off = true, time = 0.3) {
      let domArr = Array.from(this.$refs.gameDom);
      domArr.forEach(item => {
        item.style[transition] = off ? `all ${time}s` : 'none';
      })
    },
    // 选择
    select(data) {
      if (this.firstSelect === '') {
        this.firstSelect = data.colRow;
        return
      }
      this.lastSelect = data.colRow;
      this.playGame();
    }
  },
  mounted() {
    this.$warning('建设中...')
    this.init();
  }
}
</script>

<style lang="less" scoped>
.dispel {
  > .wrapper {
    width: 320px;
    height: 400px;
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    margin: auto;
    background-color: rgba(0, 0, 0, 0.3);
    overflow: hidden;
    > .box {
      width: fit-content;
      height: fit-content;
      background-color: rgba(0, 0, 0, 0.3);
      &.ac > li {
        transition: all 0.3s;
      }
      > li {
        width: 100%;
        height: 40px;
        position: absolute;
        display: inline-block;
        width: 40px;
        height: 40px;
        line-height: 40px;
        text-align: center;
        color: #fff;
        background-image: url("../assets/dispel/all.png");
        background-repeat: no-repeat;
        background-size: 100px 300px;
        box-sizing: border-box;
        &.ac {
          border: 1px solid #f00;
        }
        &.delete {
          border-radius: 50%;
          transform-origin: center center;
          transition: all 0.5s !important;
          transform: scale(0);
        }
      }
    }
  }
}
</style>